import fetchAll from './fetch'
import {
  Loading
} from 'element-ui'
const baseUrl = 'http://localhost:8080/pims'
export default async (url, params = {}, type = 'jsonp') => {
  const L = Loading.service({
    fullscreen: true,
    text: 'Please wait a moment',
    target: 'loading',
    lock: true,
    body: false
  });
  const r = await fetchAll(baseUrl + url, params, type).then(r => r)

  L.close()
  return r

}



