import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './vuex'
import fetch from './mixin/request'
import ElementUI from 'element-ui'
import * as r from './mixin/router'
import 'element-ui/lib/theme-default/index.css'
import './mixin/rem'
import { children } from './router/childMap'
Vue.use(ElementUI)


Vue.prototype.fetch = fetch //fetch
Vue.prototype.$push = r.push //
Vue.prototype.$add = r.add //
Vue.prototype.$remove = r.remove // 
Vue.prototype.$edit = r.edit // 
Vue.prototype.$check = r.check // 
Vue.config.productionTip = false

const isRank = n => children.map(r =>
  r.meta.rank == n ? r.meta.hidden = true : ''
)

