import Vue from 'vue'
import Router from 'vue-router'
import {children} from './childMap'
Vue.use(Router)

export default new Router({
  mode: 'history',
  linkActiveClass: 'active',
  routes: [{
      path: '/',
      component:() => import(/* webpackChunkName: "group-foo" */   'p/home/login'),
    },
    {
      path: '/index', //
      component: () => import(/* webpackChunkName: "group-foo" */  'p/home/index'),
      redirect: '/home',
      children
    }
  ]
})



