import store from '../vuex'
import router from '../router'
import fetch from './fetch'

export const 
check = (fileName, data) => router.push({
  name: fileName + '_check',
  params: {  data }
}),
 edit = (fileName, data) => router.push({
  name: fileName + '_edit',
  params: { data }
}),
 add = (fileName, data) => router.push({
  name: fileName + '_add',
  params: { data }
}),
 push = fileName => {
  router.push(fileName)
},
remove = function (ids, list, url) {
  this.$confirm(', ?', '', {
      confirmButtonText: '',
      cancelButtonText: '',
      type: 'warning'
    })
    .then(() => {
      // fetch() 
      for (let i in list) {
        for (let j in ids)
        if (ids[j] == list[i].id)
        store.commit('REMOVE', list, i) 
      }
      this.$message({
        type: 'success',
        message: '!'
      })
    })
    .catch(() => {
      this.$message({
        type: 'info',
        message: ''
      })
    })
}




