import fetch from '../mixin/request'

export default {
  closeBar: state => state.screenWidth < 900 ? state.showBar = false : '',
  initBar: state => state.showBar = state.screenWidth < 900 ? false : true,
  tgBar: state => state.screenWidth < 900 ? state.showBar = !state.showBar : '',
  REMOVE: (state, list, i) => list.splice(i, 1),
  ADD_PEOPLE_INFO: (state, payload) => state.peopleInfo.push(payload),
  GET_PEOPLE_INFO: async(state, list) => state.peopleInfo = await fetch('/employee/testlist'),


}



